import React from 'react';
import PropTypes from 'prop-types';
import './BaseInfo.css'
/**
 * @desc: 
 */
const BaseInfo = ({onShowImage}) => {
  return (
    <div className="baseInfo">
      <div className="baseInfo__detail">
        <h3 className="baseInfo__name">2</h3>
        <div className="baseInfo__subTitle">Detective Chinatown</div>
        <div className="baseInfo__other"> /  / </div>     
        <div className="baseInfo__other"> | 130</div>     
        <div className="baseInfo__other">20182</div>     
      </div>
      <div className="baseInfo__poster" onClick={onShowImage} style={{backgroundImage: 'url(/source/movie/asset4.jpeg)'}}></div>
    </div>
  );
};
BaseInfo.propTypes = {
  onShowImage: PropTypes.func.isRequired
}

export default BaseInfo;


