import React, { Component } from 'react';
import BaseInfo from './components/BaseInfo';
import request from '../../helpers/request';
import ScoreSummary from './components/ScoreSummary';
import CollpasibleText from '../../components/CollpasibleText';
import Artist from './components/Artist';
import Comment from './container/Comment';
import LineLink from '../../components/LineLink';
import ImageSlider from './container/ImageSlider';
import {Link} from 'react-router-dom';
import './index.css'

/**
 * BaseInfo
 * ScoreSummary 
 * CollpasibleText
 * Artist 
 * Comment 
 */

class Detail extends Component {
  state = {
    artist: [],
    showImage: false
  }
  componentDidMount(){
    this.getArist();
  }
  getArist = async () =>{
    const data = await request('/artist')
    if(data && data.length){
      this.setState({
        artist: data
      })
    }
  }

  toggleImage = () =>{
    this.setState({
      showImage: !this.state.showImage
    })
  }
  render() {
    const {artist} = this.state;
    return (
      <div className="detail">
        <div className="detail__top">
          <div className="tOperator">
            <Link to="/"> <div className="tOperator__icon tOperator__icon--back"></div> </Link>
            
            <div className="tOperator__icon tOperator__icon--share"></div>
          </div>
          <BaseInfo onShowImage={this.toggleImage} />
        </div>
        <div className="detail__content">
          <div className="detail__module">
            <ScoreSummary/>
          </div>
          <div className="detail__module">
            <CollpasibleText>
              <div className="detail__overview">
                  
              </div>
            </CollpasibleText>            
          </div>
          <div className="detail__module" style={{overflow: "hidden"}}>
            <h3 className="detail__moduleTitle"></h3>
            <Artist data={artist}/>
          </div>
          <div className="detail__module">
            <h3 className="detail__moduleTitle"></h3>
            <Comment />
          </div>
          <div className="detail__module">
            <h3 className="detail__moduleTitle"></h3>
              <LineLink href="#" title="" />
              <LineLink href="#" title="" />
              <LineLink href="#" title="" />
          </div>
        </div>
        <Link to="/seat" className="detail__buyBtn"></Link>
       {this.state.showImage && <ImageSlider onClose={this.toggleImage} />}
      </div>
    );
  }
}

export default Detail;


